import http from '@/utils/request';
export default {
  name: 'PhotoWall',
  data() {
    return {
      dataList: [],
      viewImageDialog: false,
      currentImage: {}
    };
  },
  created() {
    this.getList();
  },
  methods: {
    async getList() {
      let {
        data,
        status
      } = (await http.get(`/api/photo`)).data;
      console.log('Response:', data, status);
      if (status === 0) {
        this.dataList = data;
      } else {
        this.dataList = [];
      }
    },
    viewImage(image) {
      this.currentImage = image;
      this.viewImageDialog = true;
    }
  }
};